function CommandBlock() {
  const [copied, setCopied] = useState(false);
  const command = "npx @byronwade/beam 3000 --tor";

  const handleCopy = async () => {
    try {
      await navigator.clipboard.writeText(command);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    } catch (err) {
      console.error("Failed to copy:", err);
    }
  };

  return (
    <button
      onClick={handleCopy}
      className="group cursor-pointer inline-flex items-center gap-3 rounded-full bg-[#131313] px-5 py-3 font-mono text-sm transition-all hover:bg-[#1a1a1a] border border-white/5 hover:border-white/10"
      aria-label={copied ? "Copied to clipboard" : "Click to copy command"}
    >
      <code className="text-white">{command}</code>
      <span className="flex items-center justify-center w-5 h-5 rounded bg-white/10 group-hover:bg-white/20 transition-colors" aria-hidden="true">
        {copied ? (
          <Check className="w-3 h-3 text-green-400" />
        ) : (
          <Copy className="w-3 h-3 text-white/60" />
        )}
      </span>
    </button>
  );
}

